import React, { Component } from "react";
import pindah from "../Images/editPindahBarang.svg";
import pembayaran from "../Images/PembayaranSupplier.svg";
import returnSupplier from "../Images/returnSupplier.svg";
import { Link } from "react-router-dom";
import Skeleton from "react-loading-skeleton";

class editTransaksiSupplier extends Component {
  constructor(props) {
    super(props);
    this.state = {
      show: true,
    };
    this.test = this.test.bind(this);
  }

  test() {
    this.setState({
      show: true,
    });
  }

  render() {
    setTimeout(this.test, 1000);
    return (
      <div className="container-fluid" style={{ width: "100%" }}>
        <div className="row">
          <div className="col-lg-3">
            {this.state.show === true ? (
              <div className="card-menu">
                <div className="card-body text-center">
                  <img src={pindah} width="80%" alt="Kode Barang" />
                  <h4>
                    Edit <br /> Terima Supplier
                  </h4>
                  <Link to="/editPindahBarang">
                    <button className="btn btn-primary">Masuk</button>
                  </Link>
                </div>
              </div>
            ) : (
              <Skeleton height={300} width={450} />
            )}
          </div>
          <div className="col-lg-3">
            {this.state.show === true ? (
              <div className="card-menu">
                <div className="card-body text-center">
                  <img src={returnSupplier} width="80%" alt="Kode Barang" />
                  <h4>
                    Edit <br /> Return Supplier
                  </h4>
                  <Link to="/editPindahBarang">
                    <button className="btn btn-primary">Masuk</button>
                  </Link>
                </div>
              </div>
            ) : (
              <Skeleton height={300} width={450} />
            )}
          </div>
          <div className="col-lg-3">
            {this.state.show === true ? (
              <div className="card-menu">
                <div className="card-body text-center">
                  <img src={pembayaran} width="80%" alt="Kode Barang" />
                  <h4>
                    Edit <br /> Bayar Supplier
                  </h4>
                  <Link to="/editPindahBarang">
                    <button className="btn btn-primary">Masuk</button>
                  </Link>
                </div>
              </div>
            ) : (
              <Skeleton height={300} width={450} />
            )}
          </div>
          <div className="col-lg-12">
            <br />
          </div>
        </div>
      </div>
    );
  }
}

export default editTransaksiSupplier;
